var React = require('react')
  , Playground = require('component-playground')
  , Form = require('../src')
  , yup = require('yup');

require('./style.less')

var scope = { Form, React, Playground, yup }

class App {
  render(){
    return (
      <div>
<h1 id="formal-react">formal-react</h1>
<p>Another form validation and value management component for React. Provides <strong>minimal</strong> wiring while also 
allowing complete <strong>flexibility</strong> in how your inputs and errors are displayed. <code>{`formal-react`}</code> is built to quick and flexible. It decouples your form structure and layout from the backing data, so you can update and rearrange your markup without changing your data at all.</p>
<p><code>{`formal-react`}</code> also decouples form values form DOM state, ands ties validation to an object Schema defination. Since the schema is for the entire 
model and not just field specific validation, you can reuse it in your api utils for parsing responses, or flux stores for validating data. 
By default forms are <strong>immutable</strong>; it doesn&#39;t use any fancy persistant data structures but is easily adaptable to them if you prefer. Built on <a href="https://github.com/jquense/yup">yup</a>, <code>{`formal-react`}</code> leverages the powerful schema DSL to quickly and easily define object shapes, validation messages and typed values.</p>
<h2 id="a-quick-guide-to-yup">A Quick guide to yup</h2>
<p>Yup is a js object schema validator and object parser. The api and style is heavily inspired by Joi, an amazing library but is often too large and diffucult to use in a browser. Yup is a leaner and mostly as expressive without the server specific features. Check out the <a href="https://github.com/jquense/yup/blob/master/README.md">yup documentation</a> for a full run down of the features.</p>
<pre><code>{`var yup = require('yup')

var nameSchema = yup.string()
  .required('You must provide a name')
  .min(2, 'names must be at least 2 characters long');

var myFormModel = yup.object({
  name: yup.object({
    first: nameSchema,
    last:  nameSchema
  }),

  dateOfBirth: yup.date()
    .max(new Date(), 'You can't be born in the future!'),

  age: yup.number()
    .required('Please enter an age')
    .positive('Ages must be a positive number')
})`}
</code></pre>
<p>Once we have a Schema we are ready to build our Form.</p>
<h2 id="components">Components</h2>
<h3 id="field">Field</h3>
<p>The Field Component renders a form control and handles input value updates and validations. 
Changes to the Field value are automatically propagated back up to the containing Form 
Component.</p>
<h4 id="props">Props</h4>
<h4 id="-name-__-required-__"><code>{`name`}</code> <strong>(required)</strong></h4>
<p>The Field name, which should be path corresponding to a specific form <code>{`value`}</code> path.</p>
<pre><code>{`// given the form value 
value = {
  name: { first: '' }
  languages: ['english', 'spanish']
}

// the path "name.first" would update the &quot;first&quot; property of the form value
<Form.Field name='name.first' />

// use indexes for paths that cross arrays
<Form.Field name='languages[0]' />`}
</code></pre>
<p>type: <code>{`string`}</code></p>
<h4 id="-group-"><code>{`group`}</code></h4>
<p>Group Fields together with a common <code>{`group`}</code> name. Groups can be 
validated together, helpful for multi-part forms.</p>
<pre><code>{`<Form.Field name='first' group='name'/>
<Form.Field name='surname' group='name' />

<Form.Buttom group='name'>
  Validate Name
</Form.Button>`}
</code></pre>
<p>type: <code>{`string`}</code></p>
<h4 id="-type-"><code>{`type`}</code></h4>
<p>The Component Input the form should render. You can sepcify a builtin type 
with a string name e.g <code>{`'text'`}</code>, <code>{`'datetime-local'`}</code>, etc. or provide a Component
type class directly. When no type is provided the Field will attempt determine 
the correct input from the corresponding scheme. A Field corresponding to a <code>{`yup.number()`}</code> 
will render a <code>{`type='number'`}</code> input by default.</p>
<pre><code>{`<Form.Field type='number' />

<Form.Field type={MyInputComponent}/>`}
</code></pre>
<p>Custom Inputs should comply with the basic input api contract: set a value via a <code>{`value`}</code> prop and 
broadcast changes to that value via an <code>{`onChange`}</code> handler.</p>
<p>You can also permenantly map Components to a string <code>{`type`}</code> name via the top-level 
<code>{`addInputType()`}</code> api.</p>
<p>type: <code>{`func, string`}</code>
<em>default</em>: <code>{`''`}</code></p>
<h4 id="-events-"><code>{`events`}</code></h4>
<p>An array of event names that the Field should trigger a validation.</p>
<p>type: <code>{`array<string>`}</code>
<em>default</em>: <code>{`['onChange', 'onBlur']`}</code></p>
<h4 id="-mapvalue-"><code>{`mapValue`}</code></h4>
<p>Customize how the Field value maps to the overall Form <code>{`value`}</code>. 
<code>{`mapValue`}</code> can be a function that returns a value for <code>{`name`}</code>&#39;d path, allowing 
you to set commuted values from the Field.</p>
<pre><code>{`<Form.Field name='name'
  mapValue={ fieldValue => fieldValue.first + ' ' + fieldValue.last }
/>`}
</code></pre>
<p>You can also provide an object hash, mapping paths of the Form <code>{`value`}</code> 
to fields in the field value.</p>
<pre><code>{`<Form.Field name='name'
  mapValue={{
    'name.first': 'first', // where 'first' is a field on the input value
    'name.last':  'surname'
  }}
/>`}
</code></pre>
<p>type: <code>{`func, object`}</code></p>
<h4 id="-errorclass-"><code>{`errorClass`}</code></h4>
<p>The css class added to the Field Input when it fails validation</p>
<p>type: <code>{`string`}</code>
<em>default</em>: <code>{`'invalid-field'`}</code></p>
<h4 id="-alsovalidates-"><code>{`alsoValidates`}</code></h4>
<p>Tells the Field to trigger validation for addition paths as well as its own (<code>{`name`}</code>). 
Useful when used in conjuction with a <code>{`mapValue`}</code> hash that updates more than one value.</p>
<pre><code>{`<Form.Field name='name'
  mapValue={{
    'name.first': 'first',
    'name.last':  'surname'
  }}
  alsoValidates={['name.first', 'name.last']}
/>`}
</code></pre>
<p>type: <code>{`array<string>`}</code></p>
<h4 id="-novalidate-"><code>{`noValidate`}</code></h4>
<p>Disables validation for the Field.</p>
<p>type: <code>{`bool`}</code></p>
<h3 id="form">Form</h3>
<p>Form component renders a <code>{`value`}</code> to be updated and validated by child Fields. 
Forms can be thought of as <code>{`<input/>`}</code>s for complex values, or models. A Form aggregates 
a bunch of smaller inputs, each in charge of updating a small part of the overall model.
The Form will integrate and validate each change and fire a single unified <code>{`onChange`}</code> with the new <code>{`value`}</code>.</p>
<p>Validation messages can be displayed anywhere inside a Form with Message Components. </p>
<Playground lang="js" theme="neo" scope={scope} codeText={`var nameSchema = yup.string()
      .default('')
      .required('You must provide a name')
      .min(2, 'names must be at least 2 characters long');

var modelSchema = yup.object({
   name: yup.object({
     first: nameSchema,
     last:  nameSchema
   }),
   dateOfBirth: yup.date()
     .max(new Date(), "You can't be born in the future!"),
   age: yup.number()
     .nullable()
     .required('Please enter an age')
     .positive('Ages must be a positive number')
})

React.render(
  <Form 
    schema={modelSchema}
    defaultValue={modelSchema.default()}
  >
    <fieldset>
      <legend>Name</legend>

      <Form.Field name='name.first'/>
      <Form.Field name='name.last'/>

      <Form.Message for={['name.first', 'name.last']}/>
    </fieldset>

    <Form.Field name='dateOfBirth'/>
    <Form.Message for='dateOfBirth'/>

    <Form.Field name='age'/>
    <Form.Message for='age'/>

    <Form.Button type='submit'>Submit</Form.Button>
</Form>
, mountNode);`} />

<h4 id="props">Props</h4>
<h4 id="-value-"><code>{`value`}</code></h4>
<p>Form value object, can be left uncontrolled; 
use the <code>{`defaultValue`}</code> prop to initialize an uncontrolled form.</p>
<p>type: <code>{`object`}</code></p>
<h4 id="-onchange-"><code>{`onChange`}</code></h4>
<p>Callback that is called when the <code>{`value`}</code> prop changes.</p>
<p>type: <code>{`func`}</code></p>
<h4 id="-errors-"><code>{`errors`}</code></h4>
<p>An object hash of field errors for the form. The object should be keyed with paths
with the values being string messages or an array of messages. Errors can be left 
uncontrolled (use <code>{`defaultErrors`}</code> to set an initial value) or managed along with the <code>{`onError`}</code> callback</p>
<pre><code>{`errors={{
 "name.first": [
   'First names are required', 
   "Names must be at least 2 characters long"
 ],
}}`}
</code></pre>
<p>type: <code>{`object`}</code></p>
<h4 id="-onerror-"><code>{`onError`}</code></h4>
<p>Callback that is called when a validation error occures. It is called with an <code>{`errors`}</code> object</p>
<pre><code>{`function onError(errors){
  errors['name.first'] 
  // => 'required field', "Names must be at least 2 characters long"]
}`}
</code></pre>
<p>type: <code>{`func`}</code></p>
<h4 id="-onvalidate-"><code>{`onValidate`}</code></h4>
<p>Callback that is called whenever a validation is triggered. 
It is called <em>before</em> the validation is actually run.</p>
<pre><code>{`function onError(e){
  { event, field, args, target } = e
}`}
</code></pre>
<p>type: <code>{`func`}</code></p>
<h4 id="-onsubmit-"><code>{`onSubmit`}</code></h4>
<p>Callback that is fired when the native onSubmit event is triggered. Only relevant when 
the <code>{`component`}</code> prop renders a <code>{`<form/>`}</code> tag. onSubmit will trigger only if the form is valid.</p>
<pre><code>{`function onSubmit(e){
  // do something with valid value
}`}
</code></pre>
<p>type: <code>{`func`}</code></p>
<h4 id="-getter-"><code>{`getter`}</code></h4>
<p>A value getter function. <code>{`getter`}</code> is called with <code>{`path`}</code> and <code>{`value`}</code> and 
should return the plain <strong>javascript</strong> value at the path.</p>
<pre><code>{`function(
 path: string,
 value: any,
) -> object`}
</code></pre>
<p>type: <code>{`func`}</code>
<em>default</em>: <code>{`(path, model) => expr.getter(path)(model)`}</code></p>
<h4 id="-setter-__-required-__"><code>{`setter`}</code> <strong>(required)</strong></h4>
<p>A value setter function. <code>{`setter`}</code> is called with <code>{`path`}</code>, the form <code>{`value`}</code> and the path <code>{`value`}</code>. 
The <code>{`setter`}</code> must return updated form <code>{`value`}</code>, which allows you to leave the original value unmutated.</p>
<p>The default implementation uses the <a href="http://facebook.github.io/react/docs/update.html">react immutability helpers</a>, 
letting you treat the form <code>{`value`}</code> as immutable.</p>
<pre><code>{`function(
 path: string,
 formValue: object,
 pathValue: any
) -> object`}
</code></pre>
<p>type: <code>{`func`}</code>
<em>default</em>: <code>{`(path, model, val) => updateIn(model, toUpdateSpec(path, val))`}</code></p>
<h4 id="-delay-"><code>{`delay`}</code></h4>
<p>Time in milliseconds that validations should be debounced. Reduces the amount of validation calls
made at the expense of a slight delay. Helpful for performance.</p>
<p>type: <code>{`number`}</code>
<em>default</em>: <code>{`300`}</code></p>
<h4 id="-strict-"><code>{`strict`}</code></h4>
<p>Validations will be strict, making no attempt to coarce input values to the appropriate type.</p>
<p>type: <code>{`bool`}</code>
<em>default</em>: <code>{`true`}</code></p>
<h4 id="-novalidate-"><code>{`noValidate`}</code></h4>
<p>Turns off input validation for the Form, value updates will continue to work.</p>
<p>type: <code>{`enum<true, 'heelo', 5>`}</code></p>
<h4 id="-component-__-required-__"><code>{`component`}</code> <strong>(required)</strong></h4>
<p>A tag name or Component class the Form should render as</p>
<p>type: <code>{`func, string`}</code>
<em>default</em>: <code>{`'form'`}</code></p>
<h4 id="-schema-"><code>{`schema`}</code></h4>
<p>A Yup schema  that validates the Form <code>{`value`}</code> prop. Used to validate the form input values 
For more information about the yup api check out: <a href="https://github.com/jquense/yup/blob/master/README.md">https://github.com/jquense/yup/blob/master/README.md</a></p>
<p>type: <code>{`YupSchema`}</code></p>
<h3 id="button">Button</h3>
<p>A Form Button, for triggering validations for specific Field groups</p>
<h4 id="props">Props</h4>
<h4 id="-type-"><code>{`type`}</code></h4>
<p>The <code>{`<button/>`}</code> type</p>
<p>type: <code>{`enum<'button', 'submit'>`}</code>
<em>default</em>: <code>{`'button'`}</code></p>
<h4 id="-group-"><code>{`group`}</code></h4>
<p>Specify a group to validate, if empty the entire form will be validated.
If the button type is &#39;submit&#39; the group will be ignored and the 
entire form will be validated prior to submission.</p>
<p>type: <code>{`string`}</code></p>
<h3 id="validationsummary">ValidationSummary</h3>
<p>Display all Form validation <code>{`errors`}</code> in a single summary list.</p>
<h4 id="props">Props</h4>
<h4 id="-formatmessage-__-required-__"><code>{`formatMessage`}</code> <strong>(required)</strong></h4>
<p>An error message renderer, Should return a <code>{`ReactElement`}</code></p>
<pre><code>{`function(
  message: string, 
  idx: number, 
  messages: array
) -> ReactElement`}
</code></pre>
<p>type: <code>{`func`}</code>
<em>default</em>: <code>{`(message, idx) => <li key={idx}>{message}</li>`}</code></p>
<h4 id="-component-__-required-__"><code>{`component`}</code> <strong>(required)</strong></h4>
<p>A DOM node tag name or Component class the Message should render as.</p>
<p>type: <code>{`func, string`}</code>
<em>default</em>: <code>{`'ul'`}</code></p>
<h4 id="-errorclass-"><code>{`errorClass`}</code></h4>
<p>A css class that should be always be applied to the Message container.</p>
<p>type: <code>{`string`}</code>
<em>default</em>: <code>{`'validation-error'`}</code></p>

      </div>
    )
  }
}

React.render(<App/>, document.body)


